// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482


$color-sapphire:                     #102654;
$color-egyptian-blue:                #103aae;
$color-persian-blue:                 #2e35ac;
$color-gulf-blue:                    #2a3b63;
$color-dark-cerulean:                #14367a;
$color-resolution-blue:              #1c3374;
$color-black-russian:                #030a1a;
$color-torch-red:                    #ed0233;
$color-torch-red-2:                  #ff121f;
$color-crimson:                      #d10a42;
$color-ghost-white:                  #f8f9fc;
$color-whisper:                      #e8e8e8;
$color-light-slate-grey:             #74819c;
$color-grey:                         #787878;
$color-spindle:                      #b6bdcc;


// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);
$gradient-main-desktop:             conic-gradient(from 151deg at 16.03% 39.06%, #003d85 32.23deg, #b80027 313.3deg, #003d85);
$gradient-main-tablet:              conic-gradient(from 155deg at 20.7% 35.74%, #003d85 32.23deg, #b80027 313.3deg, #003d85);
$gradient-main-mobile:              conic-gradient(from 167deg at 35.31% 34.22%, #003d85 32.23deg, #b80027 313.3deg, #003d85);
$gradient-additional:               linear-gradient(129deg, #d10a42 0%, #0041ca 100%);
$gradient-button:                   linear-gradient(105deg, #ed0233 0%, #003eb7 100%);
$gradient-border:                   linear-gradient(151deg, #ed0233 0%, #003eb7 100%);
$gradient-95:                       linear-gradient(95deg, #ed0233 0%, #003eb7 100%);

// Typography
// ---------------------------------

//    html   
$fz-default:                        16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;

$ff-alumni-sans:                    "Alumni Sans", "Arial", sans-serif;
$ff-roboto-condensed:               "Roboto Condensed", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

//      $tf-default,     
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1200:                           1200px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
